<template>
  <div>
    <NotAuth>
      <template v-slot:error></template>
      <template v-slot:loading></template>
      <template v-slot:default>
        <div class="d-flex flex-row">
          <div style="margin-left: 20px; margin-top: 100px;">
            <Panel />
          </div>
          <v-card color="#232946" class="level d-flex flex-column align-center">
            <h2 class="level__title">MyOcean</h2>
            <div class="level__top">
              <div class="level__top__left">
                <div class="d-flex flex-row">
                  <v-icon size="48">done_outline</v-icon>
                  <h2 class="level__top__title">Уровни, ранги, звания!</h2>
                  <v-icon size="48">done_outline</v-icon>
                </div>
                <p>
                  Зарабатывайте опыт, выполняя задания, чтобы прокачивать свой уровень.
                </p>
              </div>
            </div>
            <div>
              <div
                class="level__middle"
                v-for="(item, index) in firstLevels"
                :key="index"
              >
                <div class="level__item d-flex flex-row align-center">
                  <div class="level__item__left d-flex align-center justify-center">
                    <h2>{{ index + 1 }}</h2>
                  </div>
                  <div class="level__item__right">
                    <h2>{{ item }}</h2>
                    <p>Требуется {{ targets[index] }} опыта</p>
                  </div>
                  <div class="level__item__mylevel" v-if="dataDB.level === index + 1">
                    <h2>Ваш уровень</h2>
                  </div>
                </div>
              </div>
              <v-btn
                v-if="!isOpen"
                color="#eebbc3"
                class="level__open"
                @click="isOpen = true"
                >Посмотреть все уровни</v-btn
              >
            </div>
            <div v-if="isOpen">
              <div
                class="level__middle"
                v-for="(item, index) in levels"
                :key="index"
              >
                <div class="level__item d-flex flex-row align-center">
                  <div class="level__item__left d-flex align-center justify-center">
                    <h2>{{ index + 4 }}</h2>
                  </div>
                  <div class="level__item__right">
                    <h2>{{ item }}</h2>
                    <p>Требуется {{ targets[index + 4] }} опыта</p>
                  </div>
                  <div
                    class="level__item__mylevel"
                    v-if="dataDB.level === index + 4"
                  >
                    <h2>Ваш уровень</h2>
                  </div>
                </div>
              </div>
              <v-btn
                color="#eebbc3"
                class="level__open"
                v-if="isOpen"
                @click="isOpen = false"
                >Закрыть</v-btn
              >
            </div>
          </v-card>
        </div>
      </template>
    </NotAuth>
  </div>
</template>
<script>
import "../scss/level.scss";
import NotAuth from "../components/NotAuth.vue";
import Panel from "../components/Panel.vue";
import { RankList } from "../models/models.js";

export default {
  name: "Lever",
  metaInfo: {
    title: "MyOcean - Уровни"
  },
  components: { Panel, NotAuth },
  data() {
    return {
      isOpen: false,
      dataDB: "",
      targets: [],
      firstLevels: "",
    };
  },
  created() {
    this.dataDB = JSON.parse(localStorage.getItem("user"));
    // if (!this.dataDB) return this.$router.push("/");
    let ranks = new RankList();
    this.firstLevels = ranks.shortRanks;
    this.levels = ranks.ranksFull;
    this.targets = ranks.targetFull;
  },
};
</script>
